import React, { Component } from 'react';
import { 
	NavBar, 
	Popover,
	Carousel,
  DatePicker,
} from 'antd-mobile';
import { 
  Link,
} from 'react-router-dom';
import { Icon } from 'components';
import { CarouselPage } from './components';
import './Create.css';
import { MoneyEditor } from 'scenes/create/components/MoneyEditor';

const Item = Popover.Item;

const outdata = [
	[
		[{content: "", type: "yiban"}, {content: "", type: "canyin"}, {content: "", type: "gouwu"}, {content: "", type: "fushi"}, {content: "", type: "jiaotong"}], 
		[{content: "", type: "yule"}, {content: "", type: "shejiao"}, {content: "", type: "jujia"}, {content: "", type: "tongxun"}, {content: "", type: "lingshi"}], 
		[{content: "", type: "meirong"}, {content: "", type: "yundong"}, {content: "", type: "lvxing"}, {content: "", type: "shuma"}, {content: "", type: "xuexi"}]
	],
	[
		[{content: "", type: "yiliao"}, {content: "", type: "shuji"}, {content: "", type: "chongwu"}, {content: "", type: "caipiao"}, {content: "", type: "qiche"}], 
		[{content: "", type: "bangong"}, {content: "", type: "zhufang"}, {content: "", type: "weixiu"}, {content: "", type: "haizi"}, {content: "", type: "changbei"}], 
		[{content: "", type: "liwu"}, {content: "", type: "lijin"}, {content: "", type: "huankuan"}, {content: "", type: "juanzeng"}, {content: "", type: "licai"}]
	],
	[[{content: "", type: "tianjialeimu"}]],
];

const indata = [
	[[{content: "", type: "gongzi"}, {content: "", type: "jianzhi"}, {content: "", type: "licaishouyi"}, {content: "", type: "lijin"}, {content: "", type: "qitashouru"}]],
	[[{content: "", type: "tianjialeimu"}]],
];

const TYPE = { in: '', out: '' };

export class Create extends Component {
	constructor(props) {
    super(props);
		this.state = {
			visiable: false,
			type: TYPE.out,
      category: 'yiban',
      categoryContent: '',
      money: '0',
      content: '',
		}
    this.history = this.props.history;
	}
	
	cancelClick = () => {
		this.history.push("/");
	}
	
	onSelect = (opt) => {
		// console.log(opt.props.value);
		if (opt.props.value === TYPE.out) {
			this.setState({ 
				visible: false,
				type: opt.props.value,
			});
		} else if (opt.props.value === TYPE.in) {
			this.setState({ 
				visible: false,
				type: opt.props.value,
			});
		}
  };
  
	handleVisibleChange = (visible) => {
		this.setState({
			visible,
		});
	};
  
  handleConfirm = (v) => {

  }

  handleMoneyChange = (money) => {
    this.setState({ money });
  }

  handleCategorySelect = (category, categoryContent) => {
    this.setState({ category, categoryContent });
  }

  componentWillMount() {
    if(this.history.location.state) {
      this.setState({ 
        content: this.history.location.state.content,
        money: this.history.location.state.money,
        type: this.history.location.state.type,
        category: this.history.location.state.category,
        categoryContent: this.history.location.state.categoryContent,
       });
    }
  }
	
	render() {
		return (
			<div>
				<NavBar mode="light" rightContent={<div onTouchEnd={this.cancelClick}></div>}>
          <Popover
            mask
            visible={this.state.visiable}
            overlay={[
              (<Item key="0" value="" icon={<Icon className="icon-fukuan" size="16px" />}
              data-seed="logId"></Item>), 
              (<Item key="1" value="" icon={<Icon className="icon-shoukuan" size="16px" />} style={{ whiteSpace: 'nowrap' }}></Item>), 
            ]} 
            align={{ overflow: { adjustY: 0, adjustX: 0 }, offset: [0, 20], }}
            onVisibleChange={this.handleVisibleChange}
            onSelect={this.onSelect} 
          >
            <div>
              {this.state.type}
              <Icon type="xiaosanjiao" size={12} />
            </div>
          </Popover>
				</NavBar>
        
        <DatePicker
          mode="month"
          title="select month"
          value={this.state.date} 
          onChange={date => this.setState({ date })}
        >

        </DatePicker>

				<Carousel 
					className="yime-create-carousel" 
					autoplay={false} 
					infinite selectedIndex={0} 
					swipeSpeed={35} 
				> 
					{(this.state.type === TYPE.out ? outdata : indata ).map((item, index) => (
            <div key={index} style={{ height: "210px" }} >
						  <CarouselPage contents={item} onChange={this.handleCategorySelect} value={this.state.category} type={this.state.type}/> 
            </div>
					))}
				</Carousel>
        
        <Link to={{ pathname : "/addremark", state : { category: this.state.category, categoryContent: this.state.categoryContent, money: this.state.money, type: this.state.type, content: this.state.content } }} >
          <input 
            className="yime-create-content"
            placeholder="" 
            value={this.state.content}
            onChange={() => {}}
          >
          </input>
        </Link>

				<MoneyEditor onConfirm={this.handleConfirm} onChange={this.handleMoneyChange} value={this.state.money} />
			</div>
		);
	}
}


